
.form-btn-group {
  text-align: center;
}
.good {
  margin: 1.5rem 0 0 1.5rem;
}
.good .classify {
  height: 40.9rem;
  background: #f9f9f9;
}
.good .classify .classify-title {
  height: 2.2rem;
  background: #f1f1f1;
  text-align: center;
  line-height: 2.2rem;
}
.good .classify .classify-title .title {
  font-size: 0.9rem;
  font-weight: bold;
}
.good .classify .classify-body .mdui-list-item {
  font-size: 0.7rem;
  border-bottom: 1px solid rgba(7,17,87,0.1);
}
.good .classify .classify-body .mdui-list-item.active {
  background: #878d99;
  color: #fff;
}
.good .classify .classify-body .mdui-list-item .classify-name {
  width: 75%;
}
.good .classify .classify-body .mdui-list-item .classify-toolbar {
  position: absolute;
  right: 1px;
  font-size: 0.6rem;
  padding-right: 0.5rem;
}
.good .classify .classify-body .mdui-list-item .classify-toolbar i {
  margin-right: 0.2rem;
}
.good .toolBar {
  margin: 0 1rem 1rem 1rem;
}
.good .goodTable {
  margin: 0 1rem;
}


