
.order-bd-top {
  margin: 1.5rem 1.5rem 0 0;
  height: 4px;
  background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(transparent)) border-box, repeating-linear-gradient(-45deg, #b8635e 0, #b8635e 8px, transparent 0, transparent 16px, #315baf 0, #315baf 24px, transparent 0, transparent 32px) !important;
  background: linear-gradient(transparent, transparent) border-box, repeating-linear-gradient(-45deg, #b8635e 0, #b8635e 8px, transparent 0, transparent 16px, #315baf 0, #315baf 24px, transparent 0, transparent 32px) !important;
}
.el-table .refused {
  background: #fedddd;
}
.el-table .accepted {
  background: #c9e5f5;
}
.el-table .distribute {
  background: #e2f0e4;
}
.el-table__expanded-cell {
  padding-top: 0 !important;
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.expandTitle {
  text-align: center;
  padding: 0.5rem 0;
  font-size: 0.8rem;
  font-weight: bold;
  border-bottom: 1px solid #e9e9e9;
}
.expandDetail {
  border-bottom: 1px solid #e9e9e9;
}
.expandWrpper .good-icon {
  margin: 0.5rem 0;
  width: 3rem;
  height: 3rem;
}
.expandWrpper .goodname {
  margin: 0.5rem 0;
}
.expandWrpper .goodprice {
  margin: 0.5rem 0;
  text-align: right;
}
.expandWrpper .goodprice .price {
  margin-bottom: 0.3rem;
}
.totalpriceWrapper {
  text-align: center;
  margin-top: 1rem;
}
.totalpriceWrapper .totalprice {
  color: #f36a5a;
  font-size: 0.8rem;
  font-family: Arial;
}
.totalpriceWrapper .deliverCount {
  font-size: 0.6rem;
  margin: 0.2rem 0;
}
.totalpriceWrapper .count {
  font-size: 0.6rem;
}
.remark {
  text-align: center;
  margin-top: 1rem;
}
.address {
  text-align: center;
  margin-top: 1rem;
}
.order {
  margin: 1.5rem 0 0 1.5rem;
}
.order .order-table {
  margin: 1rem;
}


