
.nav-menu {
  position: fixed;
  width: 100%;
  padding-left: 7.5rem;
  height: 3rem;
  top: 0;
  z-index: 520;
  border-bottom: 1px solid #f7f7f7;
}
.nav-menu a {
  display: block;
}
.nav-menu .user-info {
  float: right;
  margin-right: 10rem;
  line-height: 3rem;
  outline: none;
  font-size: 0;
}
.nav-menu .user-info .user-headimage {
  display: inline-block;
  vertical-align: top;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 50%;
}
.nav-menu .user-info .user-headimage img {
  border-radius: 50%;
}
.nav-menu .user-info .username {
  display: inline-block;
  vertical-align: top;
  font-size: 0.8rem;
  color: #7e8c8d;
}
.nav-menu .user-info .state-wrapper {
  display: inline-block;
  vertical-align: top;
  margin-left: 1rem;
}
.nav-menu .user-info .state-wrapper .state {
  font-size: 0.8rem;
  color: #7e8c8d;
}
.nav-menu .user-info .state-wrapper .state .fa {
  font-size: 0.7rem;
  vertical-align: text-top;
  margin-left: 0.2rem;
}
.nav-menu .user-info .user-menu-item {
  display: inline-block;
  vertical-align: top;
  margin-left: 1rem;
  font-size: 0.8rem;
  color: #7e8c8d;
}
.nav-menu .el-menu {
  height: 3rem;
}


